.body{

    transition: 3s linear;
    font-family: 'League Gothic', sans-serif;
    overflow: auto; 

    font-family: 'Uchen', serif;
    display: flex;
    flex-wrap: wrap;
    flex-direction:column;
    width: 100vw;
    justify-content:center ;
    align-items: center;
    font-size: 3rem;
    background-color: #524A4E;
    /* background-color: wheat; */
    /* background-color: RGBA(37, 41, 88); */
    /* background-color: black; */
    color: aliceblue;

}

.flexing{
/* border: 2px dashed blue;    */
/* padding:2rem ; */

}
.main{
    /* background-color:darkslategrey; */
    transition: 1s linear;
    animation: rotate 2s linear  forwards;

    display: flex;
    flex-direction: row;
align-items: flex-end;
flex: 1 1 30%; 
/* background-color: brown; */
/*  !important; */
flex-wrap: wrap;

}

.inpspan{

    flex:60%;
    font-size: 3rem;



}
.inp{
    width: 100%;
    padding:0.5rem;
    /* border: 2px solid white; */
    background-color:black;
    
    border:2px solid  black;
    
    outline: none;
    border-radius: 4px;
    color: white;

}
.inp:active{
    
    outline: none;
}
.spanbtn{
flex: 20%;
font-size: 2.8rem;



}
.btn{
    border: 2px dashed black;
background-color: black;
    width: 100%;
    padding:0.5rem;
    cursor: pointer;
    border-radius: 4px;
    color: #00FFC6;
    background-color:black;

}




.grid{
    flex: 1 1 70%; 
    transition: 2s ease-in;
    animation: rotate 2s linear  forwards;
display: flex;
flex-direction: row;
justify-content:space-around;
align-items:center;
    /* border: 2px dashed red;   */
box-shadow:0px 0px 23px 6px  rgba(0, 0, 0, 0.2);
background-color:black;
/* margin-bottom: 2px; */
    /* flex-wrap: wrap; */

}

.anime{
    flex: 1 1   30%;
    /* border: 2px solid blue; */
text-align: center;
    padding: 20px;
}
/* img */

.imgwm{
    transition: 3s linear;
    animation: rotate 2s linear  forwards;

    filter: drop-shadow(2px 1rem 0.25rem rgba(0, 0, 0, 0.2));
}

@keyframes rotate {
    0%{
        /* transform: translateZ(0);
         */
         /* filter: blur(10px); */
        transform: rotate(60deg);

    }
    100%{
        /* transform: translateZ(100); */
        transform: rotate(0deg);
        /* filter: blur(0px); */
    }
    
}

.show{
    flex: 1 1 50%;
 /* background-color: blueviolet; */
    padding: 20px;
    display: flex;
    flex-direction:column;
    justify-content: center;
    height: 100vh;
    /* flex-wrap: wrap; */

  font-size: 3rem;
}
/* Air */
.air{
    flex: 33%;
    font-size: 4rem;
    display: flex;
    /* justify-content: center; */
    text-align: center;
    flex-wrap: wrap;
    margin: 5px;



}
.max_air_div{
    flex: 50%;
    /* border: 2px solid blue; */
}

.max_air{
    font-weight: bold;
    font-size: x-large;
    color: #247881;

}

.max_air_value{
    font-weight: bold;
    font-size: x-large;
    color: #00FFC6;


}

.min_air_div{
    flex: 50%;
    /* border: 2px solid red; */

}

.min_air{
    font-weight: bold;
    font-size: x-large;
    color: #247881;
}
.min_air_value{
    font-weight: bold;
    font-size: x-large;
    color: #00FFC6;


}

/* sea
 */














.sea{
    flex: 33%;
    /* border: 2px solid blue; */
    display: flex;
    text-align: center;
    flex-wrap: wrap;
font-size: 4rem;
margin: 5px;


}
.max_sea_div{
    flex: 50%;
    /* border: 2px solid blue; */
}

.min_sea_div{
    flex: 50%;
    /* border: 2px solid red; */

}

.max_sea, .min_sea{
    font-weight: bold;
    font-size: 1em;
    color: #247881;
}
.min_sea_value,.max_sea_value{
    font-weight: bold;
    font-size: x-large;
    color: #00FFC6;

}




.humidity{
    flex: 34%;
    /* border: 2px solid  red; */
    display: flex;
    text-align: center;
    flex-wrap: wrap;
font-size: 4rem;
justify-content: center;
margin: 5px;
}

.humidify{
    flex: 100%;

}

.humidify_name{
    font-weight: bold;
    font-size: 1em;
    color: #247881;

}
.humidify_value{
    font-weight: bold;
    font-size: x-large;
    color: #00FFC6;

}






.section{
    flex: 1 1 20%;
    /* border: 2px solid violet; */
    padding: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    text-align: center;
    
}

.temp{
    flex: 50%;
    /* border: 2px solid red; */
    font-size: 20rem;
    color: #00FFC6;
}

.weathermood{
    flex: 50%;
    font-size: 3rem;
    /* border: 2px solid blue; */
    color: #247881;
    font-weight: 400;

}





@media screen and (max-width:284px) {


.body{
    display: flex;
    /* flex-wrap: wrap; */
    flex-direction:row;
 
}
.anime{
    order: 1;
}
.section{
    order: 2;
}
.show{
    order: 3;
}
    
}

@media screen and (max-width:870px){
    .grid{
        overflow: scroll;
    }


  


    



}


@media screen and (max-width:450px) {

    .body{
        display: flex;
        flex-wrap: wrap;
        justify-content:flex-start ;
        flex-direction:row;
     
    }
  
    .grid{
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        /* flex-wrap: wrap; */
        /* flex-direction:row; */
     overflow: scroll;
    }
    .anime{
        order: 1;
    }
    .section{
        order: 2;
    }
    .show{
        order: 3;
    }
    
        
    }


    @media screen and (max-width:174px){

       .grid{
        overflow: scroll;

       }
  
        .show{
            display: flex;
            flex-direction: column;
            flex: 40%;

        }



    
    
      
    
    
        
    
    
    
    }
